
import { defineComponent } from "vue";
import store from "@/store";
import "./style.scss";

export default defineComponent({
  name: "Product",
  props: {
    name: String,
    description: String,
    imageUrl: String,
    price: String,
    id: String,
  },
  methods: {
    handleClick() {
      store.commit("deleteProduct", this.id);
    },
  },
});
